.hb-admin {
  .ellipsis-1-line {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .hb-admin-scroll {
    overflow-y: auto;
  }

  .hb-admin-scroll::-webkit-scrollbar {
    width: 8px;
  }

  .hb-admin-scroll::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: #b5b6b700;
  }

  .hb-admin-scroll:hover::-webkit-scrollbar-thumb {
    background-color: #c2c3c4ff;
  }

  video {
    object-fit: cover;
  }

  .hb-table {
    table,
    tr td {
      // border:1px solid red
    }
    tbody {
      display: block;
      max-height: 68vh;
      overflow-y: auto;
      position: relative;
    }

    th {
      padding: 12px 0px 12px 12px;
    }

    td {
      vertical-align: middle !important;
      height: 64px !important;

      .video-thumbnail {
        width: 60px;
        height: 40px;
        object-fit: cover;
      }
    }

    .max-2-line {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-word;
      white-space: pre-line;
    }

    tbody::-webkit-scrollbar {
      width: 8px;
    }

    tbody::-webkit-scrollbar-thumb {
      border-radius: 4px;
      background-color: #b5b6b700;
    }

    tbody::-webkit-scrollbar-thumb {
      background-color: #c2c3c4ff;
    }

    thead,
    tbody tr {
      display: table;
      width: 100%;
      table-layout: fixed;
    }
    thead {
      width: 100%;
    }
    table {
      width: 100%;
      min-width: 1160px;
      min-height: calc(100vh - 264px);
    }

    .table-loading-overlay {
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: absolute;
      background-color: rgba(255, 255, 255, 0.4);

      &.no-items {
        background-color: transparent;
      }

      .loading-spinner-wrapper {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
      }
    }

    .tr-no-items {
      background-color: transparent;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      justify-content: center;

      .no-items-found {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 19px;

        img {
          width: 160px;
          object-fit: cover;
          margin-bottom: 80px;
        }
      }
    }
  }

  .close:focus {
    outline: none;
  }

  .card-no-border {
    padding: 0rem;
    border-color: white !important;

    .card-body {
      padding: 0.5rem;
    }
  }

  .user-status {
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 600;
  }

  .modal-header {
    font-size: 15px;
    font-weight: 500;
  }

  .react-tagsinput {
    border-radius: 0.25rem;
    border-color: #d8dbe0;
    padding: 2px 6px;

    span {
      span {
        margin: 0 5px 0 0;
        padding: 2px 4px;
        border-radius: 4px;
      }

      input {
        margin: 0;
      }
    }
  }

  .modal-thumbnail {
    width: 160px;
    height: 100px;
    display: flex;
    align-items: center;
    border: 1px solid #d8dbe0;
    font-size: 13px;
    border-radius: 0.4rem;
    overflow: hidden;

    .upload-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      cursor: pointer;

      .upload-icon-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin: 0px 10px 6px;
      }

      .upload-file-error {
        color: var(--danger);
        font-size: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }

    .icon-upload {
      width: 28px;
      height: 28px;
      opacity: 0.5;
      margin: 0 6px 0 14px;
    }

    .thumbnail-wrapper {
      position: relative;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: auto;

      .thumbnail-overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #fff4;
        top: 0;
        left: 0;
      }

      .btn-remove {
        position: absolute;
        top: 5px;
        right: 5px;
        cursor: pointer;
        font-size: 20px;
        color: #444;
      }

      .thumbnail-spinner {
        position: absolute;
        width: 1.8rem;
        height: 1.8rem;
        margin: auto;
        border: 0.24em solid #666768;
        border-right-color: transparent;
      }
    }
  }

  .upload-file-container {
    flex: 1;
    align-items: center;
    justify-content: center;
    text-align: center;
    cursor: pointer;
  }

  .drag-container {
    width: 100%;
    align-items: center;
    justify-content: center;
    padding: 50px;
    text-align: center;
    border-width: 2px;
    border-style: dashed;
    border-color: #aaa;
    border-radius: 0.25rem;
  }

  .file-error-container {
    display: flex;
    justify-content: center;
    text-align: center;
  }

  .file-error-msg-text {
    font-size: 12px;
    margin-left: 8px;
    color: #ef4b5f;
    margin-bottom: 0px;
  }

  .error-message {
    font-size: 12px;
    color: #ef4b5f;
    margin-top: 4px;
  }

  .content-container {
    padding-left: 20%;
    padding-right: 20%;
    margin-top: 12px;
  }

  .row-button-container {
    display: flex;
    justify-content: flex-end;
    margin-top: 16px;
  }

  .cancel-button {
    width: 100px;
  }

  .save-button {
    margin-left: 16px;
    width: 100px;
  }

  .full-name {
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    margin-top: 12px;
    margin-bottom: 0px;
  }

  .div-link {
    cursor: pointer;

    &:hover {
      color: #0b98d5;
    }
  }
}

textarea {
  resize: none;
}
